.catalog__container {
  display: grid;
  grid-template-columns: 250px 1fr;
  column-gap: 50px;
  padding: 80px 98px 35px;

  @media (max-width: 1290px) {
    column-gap: 10px;
    padding-left: 43px;
    padding-right: 43px;
  }

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr;
    padding: 50px 45px 35px;
  }

  @media (max-width: $mobile-width) {
    padding: 40px 15px 0;
  }
}

.catalog__list ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.catalog__promo {
  min-height: 261px;

  @media (max-width: $tablet-width) {
    padding-left: 97px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 20px;
  }
}

.catalog__promo h2 {
  margin-bottom: 6px;
  transform: none;
  letter-spacing: 0;
  font-size: 34px;
}

.catalog__promo p {
  font-size: 22px;

  @media (max-width: $tablet-width) {
    padding: 0 37px;
  }

  @media (max-width: $mobile-width) {
    padding: 0;
  }
}

.catalog__list h3 {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
}

.catalog__list p {
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
}

.catalog__breadcrumbs {
  display: grid;
  grid-template-columns: 250px 1fr;
  column-gap: 50px;
  border-top: 1px solid $light-bottom;
  padding: 36px 98px 75px;

  @media (max-width: 1290px) {
    column-gap: 10px;
    padding-left: 58px;
    padding-right: 58px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 45px;
    padding-right: 45px;
    border-top: none;
  }

  @media (max-width: $mobile-width) {
    padding: 45px 45px 65px 35px;
    column-gap: 0;
  }
}

.catalog__slider {
  grid-column: 2/3;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @media (max-width: $tablet-width) {
    grid-column: 1/3;
  }
}

.catalog__slider button {
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  background-color: inherit;
  font-family: inherit;
  border: none;
}

.catalog__slider > button {
  @media (max-width: $mobile-width) {
    display: none;
  }
}
